import React,{useState,useEffect} from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'
import SidebarComponents from '../../../../Global/Pages/Listing/Single/SidebarComponents'
import Loadable from '@loadable/component'
import HeaderSingleV5 from './Header.SingleV5.components'
import SingleListingSection from '../../../../Global/Pages/Listing/Single/SingleListingSection.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import {Link as ScrollLink} from 'react-scroll';
const OwlCarousel = Loadable(() => import('react-owl-carousel3'))
const options = {
    loop: true,
    navContainerClass:"carousel-btn-block banner-carousel-btn",
    nav: true,
    dots: false,
    animateIn:"fadeIn",
    animateOut:'slideOutDown',
    autoplayHoverPause: false,
    autoplay: false,
    margin: 30,
    smartSpeed: 1200,
    // navElements:'<div className="carousel-btn-block banner-carousel-btn"></div>',
    navText: [
      	'<span class="carousel-btn left-btn"><i class="flaticon-left-arrow-1 left"></i></span>',
      	'<span class="carousel-btn right-btn"><i class="flaticon-right-arrow right"></i></span>'
    ],
    responsive: {
        0: {
            items: 1,
        },
        480:{
            items:1,
        },
        600: {
            items: 1,
        },
        768: {
            items: 1
        },
        992: {
            items: 1
        },
        1200: {
            items: 1
        },
        1280: {
            items: 1
        }
    }
}
const BannerSingleV5 =({children})=>{
	return(
		<div className="home10-mainslider">
			<div className="container-fluid p0">
				<div className="row">
					<div className="col-lg-12">
						<div className="main-banner-wrapper home10">
							<OwlCarousel className="banner-style-one owl-theme owl-carousel" {...options}>
								{children}
							</OwlCarousel>
						</div>
					</div>
				</div>
			</div>
		</div>
	)

}
const SingleV5Section=()=> {
	const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
		document.querySelector('body').classList.toggle('body_overlay')
		document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
	useEffect(() => {
			let navtab = document.getElementById("sticky-nav-tab");
			document.addEventListener("scroll", () => {
				if (window.scrollY > 170) {
					navtab.classList.add('sticky-nav-tabs-container--top-second');
				} else {
					navtab.classList.remove('sticky-nav-tabs-container--top-second');
				}
			})			
		})

	const data = useStaticQuery(graphql`
	query{
		
		home2: file(relativePath: {eq: "home/2.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		home4: file(relativePath: {eq: "home/4.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
	}`
)
    return (
		<>
		<HeaderSingleV5/>
		<BannerSingleV5>
			<div className="slide slide-one" style={{backgroundImage: `url(${data.home4.childImageSharp.fluid.originalImg})`, height:'620px'}}></div>
			<div className="slide slide-one" style={{backgroundImage: `url(${data.home2.childImageSharp.fluid.originalImg})`, height:'620px'}}></div>
			<div className="slide slide-one" style={{backgroundImage: `url(${data.home4.childImageSharp.fluid.originalImg})`, height:'620px'}}></div>
		</BannerSingleV5>
		<section className="p0" style={{zIndex:"5"}}>
			<div className="container">
				<div className="row listing_single_row style2">
					<div className="col-lg-7 col-xl-8">
						<div className="single_property_title multicolor_style mt30-767">
							<h2>Luxury Family Home</h2>
							<p>1421 San Pedro St, Los Angeles, CA 90015</p>
						</div>
						<div className="dn db-991">
							<div id="main2">
								<span id="open2" className="flaticon-filter-results-button filter_open_btn style3" onClick={handleSidebar}> Show Filter</span>
							</div>
						</div>
					</div>
					<div className="col-lg-5 col-xl-4">
						<div className="single_property_social_share multicolor_style text-right tal-767">
							<div className="price">
								<h2>$13,000<small>/mo</small></h2>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
		<div className="sticky_heading h60 p0" >
			<div className="container">
				<div className="row">
					<div className="col-xl-12">
						<div className="sticky-nav-tabs">
							<ul className="sticky-nav-tabs-container" id="sticky-nav-tab">
								<li className="list-inline-item nav-item"><ScrollLink activeClass="active" className="sticky-nav-tab" to="details" spy={true} smooth={true} duration={500} >Details</ScrollLink></li>
								<li className="list-inline-item nav-item" ><ScrollLink activeClass="active" className="sticky-nav-tab" to="features"  spy={true} smooth={true} duration={500}> Features</ScrollLink></li>
								<li className="list-inline-item nav-item" ><ScrollLink activeClass="active" className="sticky-nav-tab" to="locations" spy={true} smooth={true} duration={500} >Locations</ScrollLink></li>
								<li className="list-inline-item nav-item" ><ScrollLink activeClass="active" className="sticky-nav-tab" to="plans" spy={true} smooth={true} duration={500} >Plans</ScrollLink></li>
								<li className="list-inline-item nav-item" ><ScrollLink activeClass="active" className="sticky-nav-tab" to="Video" spy={true} smooth={true} duration={500} >Video</ScrollLink></li>
								<li className="list-inline-item nav-item" ><ScrollLink activeClass="active" className="sticky-nav-tab" to="Reviews" spy={true} smooth={true} duration={500} >Reviews</ScrollLink></li>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</div>
		<section className="our-listing-single bgc-f7 pb30-991">
			<div className="container">
				<div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
				<div className="row">
					<div className="col-xl-12">
						<div className="listing_single_property_compare">
							<ul className="mb0">
								<li className="list-inline-item">
									<div className="icon"><span className="flaticon-transfer-1"></span></div>
									<p>Add to compare</p>
								</li>
								<li className="list-inline-item">
									<div className="icon"><span className="flaticon-envelope"></span></div>
									<p>Send an email</p>
								</li>
								<li className="list-inline-item">
									<div className="icon"><span className="flaticon-heart"></span></div>
									<p>Save</p>
								</li>
								<li className="list-inline-item">
									<div className="icon"><span className="flaticon-share"></span></div>
									<p>Share</p>
								</li>
								<li className="list-inline-item">
									<div className="icon"><span className="flaticon-printer"></span></div>
									<p>Print</p>
								</li>
							</ul>
						</div>
					</div>
				</div>
				<div className="row">
					<div className="col-md-12 col-lg-8 mt35">
						<div className="row">
							<SingleListingSection/>
						</div>
					</div>
					<div className="col-lg-4 col-xl-4 mt35">
						<SidebarComponents/>
					</div>
				</div>
			</div>
		</section>
       </>
    )
}

export default SingleV5Section
